/*--------------------------------------------------------------
## Structure - Header
--------------------------------------------------------------*/
header:not(.entry-header) {
  border-bottom: 1px solid #f1f1f1;
  padding: 1.5rem 0; }
  @media (min-width: 768px) {
    header:not(.entry-header) .header-container {
      display: flex;
      align-items: center; } }
  header:not(.entry-header) .header-container .site-branding .site-title {
    color: #414141;
