    font-weight: bold;
    line-height: 1em;
    margin: 0;
    text-transform: uppercase; }
    header:not(.entry-header) .header-container .site-branding .site-title a {
      text-decoration: none; }
  header:not(.entry-header) .header-container .site-branding .site-description {
    color: #8e8e8e;
    font-size: 1.64rem;
    margin: 0;
    text-transform: uppercase; }

/*--------------------------------------------------------------
## Structure - Primary
--------------------------------------------------------------*/
.site-content {
  padding-top: 3rem; }
  .site-content article.sticky {
    display: block; }
  .site-content article.hentry {
    margin: 0 0 3rem; }
  .site-content article .entry-meta {
    font-size: 1.6rem;
